.product {
	padding: 20px;
}

.results img {
	margin-right: 4px;
}

.imageDetails1 {
	background-color: black;
	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
}

.imageDetails p {
	color: white; /* Loading message */
}

.imageDetails .infoPanel a {
	text-decoration: none;
	color: white;
}

.imageDetails img {
	position: left;
	height: 50vh;
	top: 30%;
	left: 50%;
	transform: translateX(-50%);
	cursor: pointer;
}

.imageDetails .controls span {
	z-index: 2000;
	font-size: 18pt;
	font-weight: bold;
	top: 100px;
	position: absolute;
	cursor: pointer;
	background-color: rgba(100, 100, 100, 0.5);
	color: #eee;
	width: 100px;
	padding: 5px;
	text-decoration: none;
	display: inline-block;
	text-align: center;
}

.imageDetails .controls .prev {
	border-radius: 50%;
	background-color: #acacac;
	color: black;
}
.imageDetails .controls .prev:hover {
	background-color: #ddd;
	color: black;
}
.imageDetails .controls .next {
	border-radius: 50%;
	background-color: rgb(68, 30, 112);
	color: white;
}
.imageDetails .controls .next:hover {
	background-color: rgb(121, 37, 133);
	color: white;
}

.resultsList {
	padding: 0;
	margin: 0;
}

.resultsList li {
	list-style-type: none;
	display: inline-block;
	margin-right: 4px;
	cursor: pointer;
}

.left-col {
	display: inline-block;
	width: 50%;
}

.right-col {
	display: inline-block;

	background-color: rgba(31, 30, 30, 0.5);
	color: rgb(248, 244, 244);
}
